@import 'Styles/theme.scss';
.edit-profile {
    margin: 110px auto;
    width: 750px;
    min-height: 750px;
    .ant-form-item-label {
        height: 50px;
        line-height: 50px;
    }
    .edit-profile-title {
        text-align: left;
        font-weight: 550;
        font-size: 22px;
        margin-bottom: 40px;
        text-align: justify;
        color: $darkBlue;
    }
    .edit-profile-input {
        width: 624px;
        padding-left: 15px;
        padding-bottom: 100px;
        margin-top: 50px;
        .edit-profile-input-name {
            font-size: 16px;
            font-weight: W3;
            color: rgba(0, 0, 0, 0.8);
            line-height: 70px;
            width: 80px;
            text-align: left;
            background-color: #fff;
        }
        
        .edit-profile-input-value {
            width: 624px;
            border: 1px solid #6080b4 !important;
            height: 50px;
            border-radius: 10px;
        }
    }
}